.popup__title {
    font-family: 'Inter', Arial, sans-serif;
    font-size: 18px;
    font-weight: 900;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
    padding: 33px 0 75px 36px;
    margin: 0;
}

@media (min-width:1024px) {
    .popup__title {
        font-size: 24px;
        line-height: 29px;
        padding: 33px 0 33px 36px;
    }
}